*{box-sizing:border-box;margin:0;padding:0;font-family:'Roboto Slab', serif}.body{height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;background:#f2a154;color:#314e52}canvas{background:whitesmoke;margin:5px 10px;border-radius:3px;box-shadow:rgba(50,50,93,0.25) 0px 2px 5px -1px,rgba(0,0,0,0.3) 0px 1px 3px -1px;background-image:url(images/swagedits.png);background-size:cover;background-position:center}img,audio{display:none}.content{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;width:500px;height:800px;margin:20px 5px;box-shadow:rgba(6,24,44,0.4) 0px 0px 0px 2px,rgba(6,24,44,0.65) 0px 4px 6px -1px,rgba(255,255,255,0.08) 0px 1px 0px inset}.content .title{font-family:'Roboto Slab', serif;font-size:36px;padding:10px;border-radius:6px;padding-bottom:0px;font-weight:900;margin-top:20px}.content .author{display:flex;justify-content:center;align-items:center;margin-bottom:20px;padding:10px;text-align:center}.content .high-score-container{padding:10px 0}.content .score-container{display:flex;justify-content:space-around;align-items:center;background:#e7e6e1;box-shadow:rgba(255,255,255,0.2) 0px 0px 0px 1px inset,rgba(0,0,0,0.9) 0px 0px 0px 1px;border-radius:6px;height:30px;width:200px;font-size:large;margin-bottom:30px}.content .howtoplay{padding:20px;margin-bottom:200px}.content .socials{display:flex;justify-content:center;align-items:center}.content .socials a{font-size:40px;padding:20px;text-decoration:none;color:#314e52}
